.loader-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  background: rgba(0, 0, 0, 0.2) url("https://media.tenor.com/ACUXzGjPC-oAAAAi/sportsmanias-emoji.gif") center no-repeat;
  background-size: 90%;
  z-index: 1;
  padding: 30px;

}
.spinner {
  height: 80vh;
  z-index: -1;
  margin-top: 5px;
  color: black;
}
