.header {
  font-family: CormorantGaramondBold;
  font-size: calc(10px + 10vmin);
  margin: 40px 0px;
}

.frame {
  --s: 10px;
  padding: var(--s);
  border: calc(2 * var(--s)) solid #0000;
  outline: 1px solid #fff;
  outline-offset: calc(-1 * var(--s));
  background: conic-gradient(from 90deg at 1px 1px, #0000 25%, #fff 0);
}

a[href^='https://tech.yandex.ru/maps/mapsapi/?from=api-maps']
{
  display: None;
}
ymaps[class$="copyright__content"] {
  display: None;
}
ymaps[class$="gototaxi__pin"],
ymaps[class$="gotoymaps__pin"] {
  filter: invert(1) grayscale(0.5);
}

.gototech {
  color: yellow;
  display: None;
}

ymaps[class$="ground-pane"] {
  filter: hue-rotate(0deg) grayscale(70%);
}

.invert {
  filter: invert(100%);
}

